'use client';

import React, { useEffect, useState } from 'react';
import { Leaf, Droplets, Recycle, Waves, Sun, DollarSign, Flower2, Fish, ArrowRightLeft, Sparkles, Heart, Dot } from 'lucide-react';

export default function PrincipesPage() {
  const [visibleSections, setVisibleSections] = useState<Set<string>>(new Set());

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setVisibleSections((prev) => new Set([...prev, entry.target.id]));
          }
        });
      },
      { threshold: 0.15 }
    );

    document.querySelectorAll('[data-section]').forEach((el) => observer.observe(el));
    return () => observer.disconnect();
  }, []);

  const FeatureCard = ({
    icon: Icon,
    title,
    children,
    id,
    delay = 0
  }: {
    icon: React.ComponentType<React.SVGProps<SVGSVGElement>>;
    title: string;
    children: React.ReactNode;
    id: string;
    delay?: number;
  }) => {
    const isVisible = visibleSections.has(id);
    return (
      <div
        id={id}
        data-section
        className={`rounded-2xl p-8 bg-gradient-to-br from-emerald-500 to-white-500 text-white shadow-lg transform transition-all duration-700 ${
          isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-6'
        }`}
        style={{ transitionDelay: `${delay}ms` }}
      >
        <div className="flex items-center gap-4 mb-4">
          <div className="p-3 bg-white/20 rounded-xl">
            <Icon className="h-8 w-8" />
          </div>
          <h2 className="text-2xl font-bold">{title}</h2>
        </div>
        <div className="text-black/90">{children}</div>
      </div>
    );
  };

  return (
    <main className="bg-gradient-to-b from-emerald-50 to-sky-50 min-h-screen">
      {/* Hero */}
      <section className="py-20 text-center px-4">
        <div className="inline-flex items-center gap-2 px-6 py-3 rounded-full bg-emerald-100 text-emerald-800 font-medium mb-6">
          <Sparkles className="h-5 w-5" />
          <span>100% Naturel • 0% Chimique</span>
        </div>
        <h1 className="text-5xl md:text-6xl font-bold text-emerald-700 mb-6">
          Piscines Naturelles « Organiques »
        </h1>
        <p className="max-w-3xl mx-auto text-lg text-gray-700">
          L’alternative écologique qui transforme votre jardin en écosystème vivant, avec une eau
          cristalline et sans produits chimiques.
        </p>
      </section>

      {/* Features */}
      <section className="max-w-6xl mx-auto px-4 pb-20 space-y-8">
        <FeatureCard icon={Leaf} title="Qu'est-ce qu'une piscine organique ?" id="definition">
          Une piscine naturelle fonctionne grâce à un équilibre entre une zone de baignade et
          une zone de filtration composée de plantes aquatiques, sans aucun produit chimique. L'eau circule naturellement
          à travers les plantes, qui filtrent et purifient l'eau. à travers les plantes, qui filtrent et purifient l'eau.
          Un écosystème vivant qui offre une baignade saine et agréable.
        </FeatureCard>

        <FeatureCard icon={Recycle} title="Comment cela fonctionne ?" id="fonctionnement" delay={50}>
          Une bonne circulation de l’eau et une filtration biologique sont les deux clés pour
          maintenir une eau propre et saine.
        </FeatureCard>

        <FeatureCard icon={Droplets} title="La filtration de l'eau" id="filtration" delay={100}>
          La zone de filtration agit comme un filtre biologique composé de graviers et de plantes
          (iris, papyrus, nénuphars, lotus…) qui consomment les nutriments et empêchent la prolifération des algues.
        </FeatureCard>

        <FeatureCard icon={Recycle} title="Comment fonctionne la circulation ?" id="circulation" delay={150}>
          La circulation de l'eau se fait grâce à des pompes à air qui entraînent doucement l'eau à travers la zone de filtration.
          Ces pompes consomment peu d'énergie, garantissant un fonctionnement durable. Leur alimentation peut être
          assurée par des panneaux solaires pour une approche encore plus écologique.
          <br />Elles sont également
          sans danger pour la faune aquatique qui élira domicile dans votre piscine organique.
        </FeatureCard>

        <FeatureCard icon={Flower2} title="Les avantages" id="avantages" delay={200}>
          <ul>
            <li><Dot /> 0 produit chimique</li>
            <li><Dot /> Un écosystème vivant</li>
            <li><Dot /> Une eau claire, douce et potable</li>
            <li><Dot /> Pas de moustiques grâce à l’équilibre naturel</li>
          </ul>
        </FeatureCard>
      </section>
    </main>
  );
}
